















































import Vue from "vue";
import Component from "vue-class-component";
import "vue-property-decorator";
import VClamp from "vue-clamp";
import UpdateService from "../DBServices/UpdateService";

type Update = {
  text: string;
  title: string;
  author: string;
  createdAt: Date;
  _id: string;
};

@Component({
  components: {
    VClamp
  }
})
export default class ProjectUpdates extends Vue {
  expanded = false;
  error = "";
  updates: Update[] = [];
  async created() {
    try {
      this.updates = (await UpdateService.getUpdates()) as Update[];
      console.log(this.updates[0].createdAt);
    } catch (err) {
      this.error = err.message;
    }
  }
}
